.container {
  min-height: 100vh;
  width: 100%;
  background: url(/img/login-new-bg.ed7a3c19.jpg) no-repeat 50% fixed;
  background-size: 100% 100%;
  overflow: hidden;
  position: relative;
}
.login_form {
  position: absolute;
  width: 420px;
  max-width: 100%;
  height: 330px;
  padding: 40px 40px;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  overflow: hidden;
  background: #fff;
  border-radius: 15px;
}
.login_header_text {
  font-size: 26px !important;
  color: #333 !important;
  margin: 0 auto 30px auto;
  text-align: center !important;
  font-weight: 700;
}
.login_lang_icon {
  color: #fff;
  position: absolute;
  top: 12px;
  font-size: 18px;
  right: 0;
  cursor: pointer;
  margin: 0 !important;
  visibility: hidden;
}
.login_input {
  border: 1px solid hsla(0, 0%, 100%, 0.1);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  color: #454545;
  min-height: 45px !important;

  /* padding: 0 !important; */
}
.login_input input {
  box-shadow: 0 0 0 1000px #e5e5e5 inset !important;
}
